.Projects-main {
  min-height: 100vh;
  padding-bottom: 2rem;
  background-color: #73797d;
  padding-top: 1rem;

  @media screen and (min-width: 768px) {
    padding-top: 2rem;
    h2 {
      font-size: 3rem;
    }
  }

  @media screen and (min-width: 1800px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    h2 {
      margin-bottom: 3rem;
    }
  }
}

.Projects-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media screen and (min-width: 1800px) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10rem;
    align-items: start;
  }

  // @media screen and (min-width: 768px) {
  //   display: grid;
  //   justify-content: center;
  //   grid-template-columns: 1fr 1fr;
  // }
}
.Projects-card-hover {
  transition-duration: 0.5s;
}
.Projects-card-hover:hover {
  transform: translatey(-10px);
}
