.Navbar-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Navbar-text {
  font-size: 1rem;
  margin: 0;
  @media screen and(min-width: 768px) {
    font-size: 2rem;
  }
  @media screen and (min-width: 1800px) {
    font-size: 2.5rem;
    margin-right: 2rem;
  }
}
